.projects-container {
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background-color: #010409;

    h1{
        margin-bottom: 50px;
    }

    ul {
        display: flex;
        align-items: center;
        justify-content: center;
        // overflow-x: scroll;
        padding: 0;


        li {
            list-style: none;
            -webkit-transform: rotateX(23deg) rotateZ(-9deg) rotateY(15deg) scale3d(1, 1, -0.9);
            transform: rotateX(23deg) rotateZ(-9deg) rotateY(15deg) scale3d(1, 1, -0.9);
        }

        .toGo{

            
            .project-toGo {
            background-color: rgb(255, 251, 251);
            box-shadow: -10px 8px 2px 1px white;

            .img {
                width: 150px;
                height: 200px;
                background-image: url('../../assets/projects/logo.png');
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
        
        .button1 {
            display: flex;
            align-items: center;
            margin-top: 20px;
            border: 2px solid rgb(255, 196, 0);
            box-shadow: -5px 4px 1px 1px  rgb(255, 196, 0);

            
            div {
                padding: 10px;
                height: max-content;
                background-color: #EA3836;
            }
            
            p {
                text-align: center;
                width: 100%;
            }
            
        }
        &:hover {
            cursor: pointer;

            .project-toGo{
                transition: all 0.2s ease-in-out;
                border-radius: 2px;
                box-shadow: -4px 5px 6px 6px white;
            }

            .button1{
                    transition: all 0.2s ease-in-out;
                    background-color: rgb(255, 196, 0) ;
                    box-shadow: -2px 2px 11px 0px rgb(255, 196, 0);

                }    
        }
    }
        
    }
}